/**
 * Hero Banner component
 * @define hero-banner; weak
 */
.ecl-hero-banner {
  align-items: center;
  border-radius: 0;
  box-shadow: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 0;
  -webkit-margin-end: 0;
          margin-inline-end: 0;
  margin-top: 0;
  overflow: hidden;
  position: relative;
}

.ecl-hero-banner__credit {
  background: rgba(0, 0, 0, 0.54);
